<template>
  <div class="wrap">
    <listSearch v-model="searchData" :searchArr="searchArr" @searchConfirm="goSearch"></listSearch>
    <div class="table_box">
         <Table :columns="columns" :data="tableData">
        <template slot-scope="{ row}" slot="action">
          <div>
            <button @click="handleLook(row)">查看</button>
            <button @click="handleOver(row)">模板审核通过</button>
            <button @click="backCase(row)">模板审核驳回</button>
          </div>
        </template>
      </Table>
      <Page
        style="margin-top:20px"
        :current="pageObj.currentPage"
        :total="pageObj.total"
        :page-size="pageObj.pageSize"
        :page-size-opts="[10,20,50]"
        show-sizer
        show-elevator
        show-total
      />
    </div>
  </div>
</template>
<script>
import listSearch from "@/components/list-search/search.vue";
import {mapActions} from 'vuex'
const columns =[{title:'模板名称'}, {title:"模板文书类型"},{title:'注册人名称'},{title:'案件类型名称'},{title:"创建时间"},{title:"保存时间"},{title:"审核状态"}
,{title:"操作",width:300,slot:"action"}]
export default {
  components: {
    listSearch
  },
  data() {
    return {
      pageObj: {
        currentPage:1,
        pageSize:10,
        total:0,
      },
      searchData: {
        tempName: "",
        regName:"",
        tempType:[]
      },
      searchArr: [
        {
          type: "input",
          value: "tempName",
          label: "模板名称",
          placeholder: "请输入模板名称"
        },
        {
          type: "input",
          value: "regName",
          label: "注册名称",
          placeholder: "请输入注册名称"
        },
        {
          type: "select",
          value: "tempType",
          label: "模板类型",
          placeholder: "请选择模板类型",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ],
        }
      ],
      columns:columns,
      tableData:[]
    };
  },
  mounted () {
    // this.getList()
  },
  methods: {
    ...mapActions({
      getTemplateList: 'organizePeriod/getTemplateList',
    }),
    goSearch() {},
    handleLook(row){
        
    },
    handleOver(row){
        this.$Modal.confirm({
            title: '操作',
            content: '<div style="color:#3B4453;text-align:center;font-size:18px">确定审核通过？</div>',
            onOk: () => {
                this.$Message.success('操作成功');
                this.$Message.error('系统出错，请稍后重试')
            },
            onCancel: () => {
               
            }
        });
    },
    backCase(row){
        this.$Modal.confirm({
            title: '操作',
            content: '<div style="color:#3B4453;text-align:center;font-size:18px">确定审核驳回操作？</div>',
            onOk: () => {
                this.$Message.success('操作成功');
                this.$Message.error('系统出错，请稍后重试')
            },
            onCancel: () => {
               
            }
        });
    },
  }
};
</script>
<style lang="less" scoped>
/deep/.ivu-table th {
  background-color: #3296fa;
}
</style>
